<template>
  <div class="mainContent">
    <div class="main_machine">
      <com-product  v-bind:data="dataCar"></com-product>
    </div>
  </div>
</template>
<script>
import "@/styles/TradeShow.css";
import { mapGetters } from "vuex";
import product from "@/components/product";

export default {
  name: "Car", //汽车
  computed: {
    ...mapGetters(["dataCar"])
  },
  components: {
    "com-product": product
  },
  data() {
    return {};
  }
};
</script>
<style>
</style>




