<template>
  <div class="industry_application_other">
    <div class="other_content">
      <p>我们的服务</p>
      <P>
        <span></span>
        <p class="partners_text">提供来样加工，新产品研发</p>
      </P>
    </div>
  </div>
</template>
<script>
export default {
  name: "Other",
}
</script>
<style>
.industry_application_other {
  width: 100%;
  min-width: 1200px;
  max-width: 2000px;
}
.industry_application_other .other_content {
  width: 70%;
  height: 600px;
  margin: 0 auto;
  padding-top: 70px;
  text-align: center;
  background: #fbfbfb;
}
.industry_application {
  padding-bottom: 0px;
}
.other_content {
  width: 100%;
  text-align: center;
}
.other_content p {
  text-align: center;
  position: relative;
  font-size: 22px;
}
.other_content p:nth-child(1) {
  font-weight: 700;
  width: 120px;
  margin: 0 auto;
  color: #6490e8;
  margin-bottom: 10px;
}
.other_content p span {
  width: 70%;
  border: 0.5px solid #6490e8;
  height: 1px;
  display: inline-block;
  margin: 0 auto;
  z-index: 1;
}
.other_content .partners_text {
  width: 245px;
  margin: 0 auto;
  background: #fff;
  z-index: 2;
  font-size: 18px;
  color: #a3c1ff;
  line-height: 40px;
  margin-top: -30px;
}
</style>





