<template>
  <div class="mainContent">
    <div class="main_machine">
      <com-product  v-bind:data="dataDrywasher"></com-product>
    </div>
  </div>
</template>
<script>
import "@/styles/TradeShow.css";
import { mapGetters } from "vuex";
import product from "@/components/product";


export default {
  name: "DryWasher", //干洗机
  components: {
    "com-product": product
  },
  computed: {
    ...mapGetters(["dataDrywasher"])
  },
  mounted() {},
  data() {
    return {};
  }
};
</script>
<style>
</style>




