<template>
  <div class="industry_application">
    <div class="top_banner">
      <img src="@/assets/hangyeyingyong.png">
    </div>
    <div class="banner_nav">
      <el-menu
        class="el-menu-demo item"
        mode="horizontal"
        :default-active="$route.path"
        @select="handleSelect"
        background-color="#3B3B3B"
        text-color="#fff"
        active-text-color="#6490E8"
        router
      >
        <el-menu-item class="itemChild" index="/dryer">{{$t("TradeShow.drywasher")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/washing">{{$t("TradeShow.washing")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/elevator">{{$t("TradeShow.elevator")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/printer">{{$t("TradeShow.printer")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/car">{{$t("TradeShow.car")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/fitness">{{$t("TradeShow.fitness")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/agriculture">{{$t("TradeShow.agriculture")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/forestry">{{$t("TradeShow.forestry")}}</el-menu-item>
        <el-menu-item class="itemChild" index="/other">{{$t("TradeShow.other")}}</el-menu-item>
        
      </el-menu>
    </div>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import "@/styles/SearchPAndTradeS.css";

export default {
  name: "TradeShow",
  computed: {
    ...mapGetters(["machine"])
  },
  mounted() {
    // 根据产品总列表配置各个分类的产品数据
    let vm = this;
    setTimeout(() => {
      vm.classifyDataUp(this.machine);
    }, 500);
  },
  updated() {
    let vm = this;
    setTimeout(() => {
      vm.classifyDataUp(this.machine);
    }, 500);
  },
  methods: {
    handleSelect(key, keyPath) {
    },
    classifyDataUp(dataList) {
      // console.log("classifyDataUp");
      let vm = this;
      let dataListLength = dataList.length;
      let i;
      let dataDrywasher = [];
      let dataWashing = [];
      let dataElevator = [];
      let dataPrinter = [];
      let dataCar = [];
      let dataFitness = [];
      let dataAgriculture  = [];
      let dataForestry  = [];
      let result1;
      let result2;
      let result3;
      let result4;
      let result5;
      let result6;
      let result7;
      let result8;
      for (i = 0; i < dataListLength; i++) {
        result1 = dataList[i].usageScenarioIdArr.indexOf("1");
        result2 = dataList[i].usageScenarioIdArr.indexOf("2");
        result3 = dataList[i].usageScenarioIdArr.indexOf("3");
        result4 = dataList[i].usageScenarioIdArr.indexOf("4");
        result5 = dataList[i].usageScenarioIdArr.indexOf("5");
        result6 = dataList[i].usageScenarioIdArr.indexOf("6");
        result7 = dataList[i].usageScenarioIdArr.indexOf("7");
        if (result1 != -1) {
          dataDrywasher.push(dataList[i]);
        }
        if (result2 != -1) {
          dataWashing.push(dataList[i]);
        }
        if (result3 != -1) {
          dataElevator.push(dataList[i]);
        }
        if (result4 != -1) {
          dataPrinter.push(dataList[i]);
        }
        if (result5 != -1) {
          dataCar.push(dataList[i]);
        }
        if (result6 != -1) {
          dataFitness.push(dataList[i]);
        }
        if (result7 != -1) {
          dataAgriculture.push(dataList[i]);
        }
        if (result8 != -1) {
          dataForestry.push(dataList[i]);
        }
      }

      vm.$store
        .dispatch("set_dataDrywasher", dataDrywasher)
        .then(function(response) {
          // console.log("classifyDataUp - set_dataDrywasher");
        })
        .catch(function(error) {
          console.info(error);
        });
      vm.$store
        .dispatch("set_dataWashing", dataWashing)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
      vm.$store
        .dispatch("set_dataElevator", dataElevator)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
      vm.$store
        .dispatch("set_dataPrinter", dataPrinter)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
      vm.$store
        .dispatch("set_dataCar", dataCar)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
      vm.$store
        .dispatch("set_dataFitness", dataFitness)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
       vm.$store
        .dispatch("set_dataAgriculture", dataAgriculture)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
      vm.$store
        .dispatch("set_dataForestry", dataForestry)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
    },
  },
  data() {
    return {
      activeIndex: "/drywasher"
    };
  }
};
</script>
<style scoped>
.item {
  margin-top: 3px;
  padding-top: 3px;
}
.itemChild {
  margin-top: -3px;
  padding-top: -3px;
}
</style>




