<script>
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      data: null,
      dataTableImg: null,
      detailImg: null,
      productName: null, //产品名称
      characteristic: null, //产品特点
      productMaterial: null, //产品材质
      market: null, //应用市场
      product_description: null, //产品描述是false或者true
      curvedToothIsShow: false, //曲线齿同步带
      STSSuperIsShow: false, //STSS齿同步带
      trapezoidalToothIsShow: false, //梯形齿同步带
      ribbedBeltIsShow: false, //多契带
      trimmingVtapeIsShow: false, //切边V带
    };
  },
  computed: {
    ...mapGetters(["currentProduct2", "machine"])
  },
  watch: {
    // machine() {
    //   //
    // }
  },
  created() {
    let vm = this;
    setTimeout(() => {
      vm.updateShow(vm.currentProduct2, vm.machine);
    }, 600);
    vm.showPdfTable(vm.currentProduct2)
  },
  updated() {
    let vm = this;
    setTimeout(() => {
      vm.updateShow(vm.currentProduct2, vm.machine);
    }, 600);
    vm.showPdfTable(vm.currentProduct2)
  },
  methods: {
    escapeDetail() {
      this.$router.back(-1);
    },
    updateShow(currentProduct2, machine) {
      // console.log("currentProduct2");
      let vm = this;
      let length = machine.length;
      let i;
      for (i = 0; i < length; i++) {
        if (currentProduct2.productId == machine[i].productId) {
          vm.dataTableImg = machine[i].dataTableImg;
          vm.detailImg = machine[i].detailsImg[0];
          vm.productName = machine[i].productName;
          vm.characteristic = machine[i].characteristic;
          vm.productMaterial = machine[i].productMaterial;
          vm.market = machine[i].market;
          vm.product_description = machine[i].product_description
        }
      }
    },
    showPdfTable(currentProduct2) {
      let productName = currentProduct2.productName  //产品名称
      if (productName === "曲线齿同步带" || productName === "Curved tooth timing belt") {
        this.curvedToothIsShow = true
      } else if (productName === "STS超级扭距同步带" || productName === "STS Super torque sync belt") {
        this.STSSuperIsShow = true
      } else if (productName === "梯型齿同步带" || productName === "Trapezoidal tooth timing belt") {
        this.trapezoidalToothIsShow = true
      } else if (productName === "多楔带" || productName === "Ribbed belt") {
        this.ribbedBeltIsShow = true
      } else if (productName === "切边V带 窄V带" || productName === "Trimming v tape") {
        this.trimmingVtapeIsShow = true
      }
    },
  }
};
</script>

<style scoped>
.white {
  color: #ffffff;
}
.top_banner {
  width: 100%;
  margin-bottom: 40px;
  margin-top: -40px;
}
.detail_introduce {
  margin-bottom: 30px;
  overflow: hidden;
  height: 300px;
}
.detail_introduce .detail_text,
.detail_introduce .detail_img {
  float: left;
}
.detail_introduce .detail_text {
  width: 50%;
}
.detail_introduce .detail_img {
  width: 45%;
  position: relative;
  height: 300px;
}
.detail_introduce .detail_img .dataTableImg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.detail_introduce .detail_text ul li {
  list-style: none;
  line-height: 30px;
  font-size: 16px;
  margin-bottom: 10px;
}
.detail_introduce .detail_text ul li span:nth-child(1) {
  font-weight: bold;
  font-size: 18px;
}
.intro {
  text-align: center;
  font-size: 16px;
}
.detail_body {
  width: 100%;
  max-width: 2000px;
  min-width: 1200px;
  background: #f9f9f9;
  padding: 40px 0px;
}
.main_detail {
  width: 75%;
  margin: 0px auto;
  padding: 20px 0px 30px 0px;
  background-color: #fff;
}
.detail_top {
  margin: 0px auto 20px auto;
  width: 95%;
  color: #6490e8;
  font-size: 16px;
  overflow: hidden;
}
.main_detail .product_size,
.main_detail .product_property_title {
  text-align: center;
  font-size: 16px;
  margin-bottom: 10px;
  font-weight: bold;
}
.main_detail .product_property {
  margin-bottom: 35px;
}

.detail_top .detail_top_title {
  float: left;
  font-size: 24px;
}
.detail_top .detail_top_icon {
  float: right;
  font-size: 26px;
  cursor: pointer;
}
/* 表格开始 */
.more_detail_pdf {
  margin-top: 20px;
}
.more_detail_pdf span {
  font-size: 16px;
  color: #6490e8;
  margin-top: 20px;
}
.main_detail table.gridtable {
  width: 704px;
  margin: 10px auto;
  font-family: verdana, arial, sans-serif;
  font-size: 14px;
  color: #333333;
  border-width: 1px;
  border-color: #666666;
  border-collapse: collapse;
}
.main_detail table.curved_tooth {
  width: 700px;
}
.main_detail table.STS_super_torque {
  width: 858px;
}
.main_detail table.trapezoidal_tooth {
  width: 858px;
}
.main_detail table.ribbed_belt {
  width: 698px;
}
.main_detail table.trimming_v_tape {
  width: 858px;
}
.main_detail table.gridtable th {
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #666666;
  background-color: #bcd6ee;
}
.main_detail table.gridtable td {
  border-width: 1px;
  padding: 8px;
  border-style: solid;
  border-color: #666666;
  background-color: #fff2cc;
  text-align: center;
}
.main_detail table.gridtable .remark_td {
  text-align: left;
}
.main_detail .get_more_pdf td a {
  text-decoration: none;
}
.main_detail table.gridtable .disabled_td {
  color: #6490e8;
}
/* 表格结束 */
</style>




