<template>
  <div class="mainContent">
    <div class="main_machine">
      <div class="main_machine">
        <com-product v-bind:data="dataForestry"></com-product>
      </div>
    </div>
  </div>
</template>
<script>
import "@/styles/TradeShow.css";
import { mapGetters } from "vuex";
import product from "@/components/product";

export default {
  name: "Forestry", //洗衣机
  computed: {
    ...mapGetters(["dataForestry"])
  },
  components: {
    "com-product": product
  },
  data() {
    return {};
  }
};
</script>
<style>
</style>




