<template>
  <div>
    <div class="product_show" v-for="(item,index) in data" :key="index">
      <div class="product_outer">
        <div class="product_title">
          <span class="product_name">{{indexSetting(index)}}.</span>
          <span class="product_name">{{item.productName}}</span>
          <!-- <span class="title_numberX">{{indexSetting(index)}}.</span> -->
          <!-- <span class="title_PnameX">{{item.productName}}</span> -->
          <span
            class="show_detail"
            v-show="item.detail && item.dataTableImg.length != 0"
            @click="goDetails('/details1',item)"
          >{{$t("product.product_details")}}</span>
          <span class="show_detail" v-show="item.dataTableImg.length == 0">{{$t("product.noIntro")}}</span>
        </div>
        <div class="img_li">
          <img :src="item.img">
        </div>
        <ul>
          <li class="productMaterial_li">
            <span>{{$t("product.product_material")}}</span>
            <p v-show="item.productMaterial != ''">{{item.productMaterial}}</p>
            <p v-show="item.productMaterial == ''">{{$t("product.noIntro")}}</p>
          </li>
          <li class="market_li">
            <span>{{$t("product.market_application")}}</span>
            <p v-show="item.market != ''">{{item.market}}</p>
            <p v-show="item.market == ''">{{$t("product.noIntro")}}</p>
          </li>
          <li class="characteristic_li">
            <span>{{$t("product.product_feature")}}</span>
            <p v-show="item.characteristic != ''">{{item.characteristic}}</p>
            <p v-show="item.characteristic == ''">{{$t("product.noIntro")}}</p>
          </li>
        </ul>
        <div
          class="detail_li"
          v-show="item.detail && item.dataTableImg.length != 0"
          @click="goDetails('/details1',item)"
        >{{$t("product.product_details")}}</div>
        <div
          class="detail_li"
          v-show="item.detail && item.dataTableImg.length == 0"
        >{{$t("product.noDetails")}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import "@/styles/TradeShow.css";
export default {
  name: "product",
  props: ["data"],
  data() {
    return {};
  },
  methods: {
    goDetails(link, item) {
      let vm = this;
      vm.$store
        .dispatch("set_currentProduct", item)
        .then(function(response) {
          vm.$router.push({
            path: link
            // query: {
            //   data: item
            // }
          });
        })
        .catch(function(error) {
          console.info(error);
        });
    },
    indexSetting(index) {
      let value;
      index++;
      if(index>=1 && index<=9){
        value = "0"+ String(index)
      }
      else{
        value = String(index);
      }
      return value;
    }
  }
};
</script>
<style>
</style>



